/* Style the header with a grey background and some padding */
.header {
  overflow: hidden;
  background-color:  rgb(2, 2, 114);
  padding: 10px 10px;
  /* border: 1px solid  rgb(198, 198, 201); */
  position: fixed;
  width: 100%;
  z-index: 2000;
  top: 0;
  left: 0;
  padding-top: 0;
  height: 60px;
  padding-right: 30px;
  border-bottom: 2px solid white;
}
/* 
body {
  background-color: var(--background);
  color: var(--text);
} */

.hd{
  position: fixed;
  margin-top: 13px;
  right: 0px;
  cursor: pointer;
}

.logo-header{
  height: 40px;
  width: 100px;
}

/* Style the header links */
.header .header-move {
  float: left;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 15px;
  line-height: 25px;
  font-weight: bold;
  cursor: pointer;
}

.header-move:hover{
  color: rgb(253, 223, 140);
}

#hd1:hover, #hd2:hover{
  color: yellow;
}

#logo {
  float: left;
  color:  white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 25px; 
  line-height: 25px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.header a.logo {
  font-size: 25px;
  font-weight: bold;
}

/* Change the background color on mouse-over */
.header a:hover {
  font-weight: bold;
  color: black;
  border-bottom: 2px solid black;
}

/* Style the active/current link*/
.header a.active {
  color: blue;
  border-bottom: 2px solid blue;
  font-weight: bold;
}

/* Float the link section to the right */
.header-right {
  float: right;
}

.iden{
  margin-left: 4px;
}

#hd2{
  margin-right: 22px;
}


/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */ 
@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}


@media screen and (min-width: 0px) and (max-width: 440px) {
.iden{
  display: none;
}

.icone-header{
  margin-top: -15px ; 
  font-size:28px; 
  margin-right:4px;
}

.header-right{
 right: 15px;
 position: absolute; 
}

#hd1{
  margin-right: -12px;
}

.hd{
  margin-top: 16px;
}


}

#header{
  padding: 0 !important;
  margin: 0 !important;
}