#bas {
    display: none;
}




#bas .nav {
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 55px;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    background-color: #ffffff;
    display: flex;
    overflow-x: auto;
    /* border-top: 1px solid rgb(176, 176, 176); */
}

.modale-row .interne{
    cursor: pointer;
    padding-bottom: 10px ;
    border-radius: 5px 5px 5px 5px;
    border: 1px solid rgb(235, 235, 235);
    color: white;
    margin:0;
    height: 60px;
    margin-left: 3px;
    margin-right: 3px;
    margin-bottom: 5px;
    background-color:  rgb(2, 2, 114);
}

.bas-block{
    padding: 0;
    
}

.modale-row .interne:hover{
    background-color: white;
    color:  rgb(2, 2, 114);
    border-color: rgb(2, 2, 114);
}


#bas .nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    min-width: 50px;
    overflow: hidden;
    white-space: nowrap;
    font-family: sans-serif;
    font-size: 13px;
    color:   rgb(2, 2, 114);
    font-weight: bold;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
    transition: background-color 0.1s ease-in-out;
    border: 1px solid rgb(231, 233, 233);
    cursor: pointer;
    border-radius: 10px 10px 0px 0px;
}

#bas .nav__link:hover {
    background-color: #e2fcfa;
}

#bas .nav__link--active {
    color:   green;
    font-weight: bold;
}

#bas .nav__icon {
    font-size: 18px;
}

