#left{
  margin: 0%;
  margin-top: 73px;
  height: 200px;
  width: 100%;
  padding: 0%;
  text-align: left;
}

#left ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#left li span {
  display: block;
  color: var(--headerColor1);
  padding: 2px 8px;
  text-decoration: none;
  cursor: pointer;
}



/* Change the link color on hover */
#left li span:hover {
  color: var(--headerColor2);
  border-left: 4px solid var(--headerColor2)
}



#left .avec{
   border-left:3px solid var(--headerActuelle); 
   color: var(--headerActuelle) 
}

#left .avec:hover{
  border-left:3px solid var(--headerColor2); 
  color: var(--headerColor2); 
}


#left .sans{
  border-left: none; 
  color: var(--headerColor1)
}