
   .panier{
    margin-top: 0px;
    padding: 10px;
   }

   .pr{
	color: var(--text)
   }

   .panier .valider{
    color: white;
	font-size: 15px;
    border-radius: 10px;
	padding: 10px;
	padding-left: 20px;
	padding-right: 20px;
    background-color: rgb(2, 2, 114);
	cursor: pointer;
   }

   .panier .valider:hover{
	background-color: green;
   }

   #back {
    	color: #fff;
    	background-color: rgb(2, 2, 114);
    	cursor: pointer;
		border-radius: 5px 5px 5px 5px;
		padding: 10px;
		padding-left: 12px;
		padding-right: 20px;
	}

	.panier .remove{
		color: var(--remove);
		cursor: pointer;
	}

	.panier .remove:hover{
		color: red;
	}


	#back:hover {
    	color: #fff;
    	background-color: rgb(201, 158, 1);
		text-decoration: none  ;
	}


.panier img {
	 max-width: 100%;
}
.panier .cf:before, .panier .cf:after {
	 content: " ";
	 display: table;
}
.panier .cf:after {
	 clear: both;
}
.panier .cf {
	 *zoom: 1;
}
.panier .wrap {
	 width: 75%;
	 max-width: 960px;
	 margin: 0 auto;
	 padding: 5% 0;
	 margin-bottom: 5em;
}
.panier .projTitle {
	 font-family: 'Montserrat', sans-serif;
	 font-weight: bold;
	 text-align: center;
	 font-size: 2em;
	 padding: 1em 0;
	 /* border-bottom: 1px solid #dadada; */
	 letter-spacing: 3px;
	 text-transform: uppercase;
}
.panier .projTitle span {
	 font-family: 'Droid Serif', serif;
	 font-weight: normal;
	 font-style: italic;
	 text-transform: lowercase;
	 color: #777;
}
.panier .heading {
	 padding: 1em 0;
	 /* border-bottom: 1px solid #d0d0d0; */
}
.panier .heading h1 {
	 font-family: 'Droid Serif', serif;
	 font-size: 2em;
	 float: left;
}
.panier .heading a.continue:link, .panier .heading a.continue:visited {
	 text-decoration: none;
	 font-family: 'Montserrat', sans-serif;
	 letter-spacing: -0.015em;
	 font-size: 0.75em;
	 padding: 1em;
	 color: #fff;
	 background: rgb(2, 2, 114);
	 font-weight: bold;
	 border-radius: 50px;
	 float: right;
	 text-align: right;
	 -webkit-transition: all 0.25s linear;
	 -moz-transition: all 0.25s linear;
	 -ms-transition: all 0.25s linear;
	 -o-transition: all 0.25s linear;
	 transition: all 0.25s linear;
}
.panier .heading a.continue:after {
	 content: "\276f";
	 padding: 0.5em;
	 position: relative;
	 right: 0;
	 -webkit-transition: all 0.15s linear;
	 -moz-transition: all 0.15s linear;
	 -ms-transition: all 0.15s linear;
	 -o-transition: all 0.15s linear;
	 transition: all 0.15s linear;
}
.panier .heading a.continue:hover, .panier .heading a.continue:focus, .panier .heading a.continue:active {
	 background: #f69679;
}
.panier .heading a.continue:hover:after, .panier .heading a.continue:focus:after, .panier .heading a.continue:active:after {
	 right: -10px;
}
.panier .tableHead {
	 display: table;
	 width: 100%;
	 font-family: 'Montserrat', sans-serif;
	 font-size: 0.75em;
}
.panier .tableHead li {
	 display: table-cell;
	 padding: 1em 0;
	 text-align: center;
}
.panier .tableHead li.prodHeader {
	 text-align: left;
}
.panier .cart {
	 padding: 1em 0;
}
 .cart .items {
	 display: block;
	 width: 100%;
	 /* vertical-align: middle; */
	 padding: 1.5em;
	 border: 1px solid #a6a6a6;
     border-radius: 10px 10px 10px 10px;
     
}
.panier .cart .items.even {
	 background: #fafafa;
}
.panier .cart .items .infoWrap {
	 display: table;
	 width: 100%;
}
.panier .cart .items .cartSection {
	 display: table-cell;
	 vertical-align: middle;
}
.panier .cart .items .cartSection .itemNumber {
	 font-size: 0.75em;
	 color: #777;
	 margin-bottom: 0.5em;
}
.panier .cart .items .cartSection h3 {
	 font-size: 1em;
	 font-family: 'Montserrat', sans-serif;
	 font-weight: bold;
	 text-transform: uppercase;
	 letter-spacing: 0.025em;
}
.panier .cart .items .cartSection p {
	 display: inline-block;
	 font-size: 0.85em;
	 color: #777;
	 font-family: 'Montserrat', sans-serif;
}
.panier .cart .items .cartSection p .quantity {
	 font-weight: bold;
	 color: #333;
}
.panier .cart .items .cartSection p.stockStatus {
	 color: rgb(2, 2, 114);
	 font-weight: bold;
	 padding: 0.5em 0 0 1em;
	 text-transform: uppercase;
}
.panier .cart .items .cartSection p.stockStatus.out {
	 color: #f69679;
}
.panier .cart .items .cartSection .itemImg {
	 width: 4em;
	 float: left;
}
.panier .cart .items .cartSection.qtyWrap, .panier .cart .items .cartSection.prodTotal {
	 text-align: center;
}
.panier .cart .items .cartSection.qtyWrap p, .panier .cart .items .cartSection.prodTotal p {
	 font-weight: bold;
	 font-size: 1.25em;
}
.panier .cart .items .cartSection input.qty {
	 width: 2em;
	 text-align: center;
	 font-size: 1em;
	 padding: 0.25em;
	 margin: 1em 0.5em 0 0;
}
.panier .cart .items .cartSection .itemImg {
	 width: 8em;
	 display: inline;
	 padding-right: 1em;
}
.panier .special {
	 display: block;
	 font-family: 'Montserrat', sans-serif;
}
.panier .special .specialContent {
	 padding: 1em 1em 0;
	 display: block;
	 margin-top: 0.5em;
	 border-top: 1px solid #dadada;
}
.panier .special .specialContent:before {
	 content: "\21b3";
	 font-size: 1.5em;
	 margin-right: 1em;
	 color: #6f6f6f;
	 font-family: helvetica, arial, sans-serif;
}
.panier a.remove {
	 text-decoration: none;
	 font-family: 'Montserrat', sans-serif;
	 color: #fff;
	 font-weight: bold;
	 background: #e0e0e0;
	 padding: 0.5em;
	 font-size: 0.75em;
	 display: inline-block;
	 border-radius: 100%;
	 line-height: 0.85;
	 -webkit-transition: all 0.25s linear;
	 -moz-transition: all 0.25s linear;
	 -ms-transition: all 0.25s linear;
	 -o-transition: all 0.25s linear;
	 transition: all 0.25s linear;
}
.panier a.remove:hover {
	 background: #f30;
}
.panier .promoCode {
	 border: 2px solid #efefef;
	 float: left;
	 width: 35%;
	 padding: 2%;
}
.panier .promoCode label {
	 display: block;
	 width: 100%;
	 font-style: italic;
	 font-size: 1.15em;
	 margin-bottom: 0.5em;
	 letter-spacing: -0.025em;
}
.panier .promoCode input {
	 width: 85%;
	 font-size: 1em;
	 padding: 0.5em;
	 float: left;
	 border: 1px solid #dadada;
}
.panier .promoCode input:active, .panier .promoCode input:focus {
	 outline: 0;
}
.panier .promoCode a.btn {
	 float: left;
	 width: 15%;
	 padding: 0.75em 0;
	 border-radius: 0 1em 1em 0;
	 text-align: center;
	 border: 1px solid rgb(2, 2, 114);
}
.panier .promoCode a.btn:hover {
	 border: 1px solid #f69679;
	 background: #f69679;
}

.panier .delete{
    position: absolute;
    left: 35px;
    margin-top: -22px;
}


@media screen and (min-width: 0px) and (max-width: 440px) {
    .panier .delete {
        display: none;
   }


   .panier .somme{
    font-size: 16px;
   }

   .heading{
    display: none;
   }

   .panier{
    margin-top: 80px;
    padding: 0;
   }


}


@media screen and (min-width: 0px) and (max-width: 640px) {
	.panier{
		margin-top: 30px;
		padding: 0;
	   }
}



.panier .btn:link, .btn:visited {
	 text-decoration: none;
	 font-family: 'Montserrat', sans-serif;
	 letter-spacing: -0.015em;
	 font-size: 1em;
	 padding: 1em 3em;
	 color: #fff;
	 background:  rgb(2, 2, 114);
	 font-weight: bold;
	 border-radius: 50px;
	 float: right;
	 text-align: right;
	 -webkit-transition: all 0.25s linear;
	 -moz-transition: all 0.25s linear;
	 -ms-transition: all 0.25s linear;
	 -o-transition: all 0.25s linear;
	 transition: all 0.25s linear;
}
.panier .btn:after {
	 content: "\276f";
	 padding: 0.5em;
	 position: relative;
	 right: 0;
	 -webkit-transition: all 0.15s linear;
	 -moz-transition: all 0.15s linear;
	 -ms-transition: all 0.15s linear;
	 -o-transition: all 0.15s linear;
	 transition: all 0.15s linear;
}
.panier .btn:hover, .panier .btn:focus, .panier .btn:active {
	 background: #f69679;
}
.panier .btn:hover:after, .panier .btn:focus:after, .panier .btn:active:after {
	 right: -10px;
}
.panier .promoCode .btn {
	 font-size: 0.85em;
	 padding: 0.5em 2em;
}
/* TOTAL AND CHECKOUT */
.panier .subtotal {
	 float: right;
	 width: 100%;
}
.panier .subtotal .totalRow {
	 padding: 0.5em;
	 text-align: right;
}
.panier .subtotal .totalRow.final {
	 font-size: 1.25em;
	 font-weight: bold;
}
.panier .subtotal .totalRow span {
	 display: inline-block;
	 padding: 0 0 0 1em;
	 text-align: right;
}
.panier .subtotal .totalRow .label {
	 font-family: 'Montserrat', sans-serif;
	 font-size: 0.85em;
	 text-transform: uppercase;
	 color: #777;
}
.panier .subtotal .totalRow .value {
	 letter-spacing: -0.025em;
	 width: 35%;
}


@media only screen and (max-width: 39.375em) {
    .panier .wrap {
        width: 98%;
        padding: 2% 0;
   }
}

.removeWrap{
    color: rgb(148, 0, 0);
    padding-top: 5px;
    margin-top: 27px;
    cursor: pointer;
}

.dt{
    padding: 0!important;
}

.btl{
    background-color: rgb(2, 2, 114);
    color: white;
    border: 1px solid rgb(2, 2, 114);
    width: 32px;
    border-radius: 5px 5px 5px 5px;
}

.btr{
    background-color: rgb(2, 2, 114);
    color: white;
    border: 1px solid rgb(2, 2, 114);
    width: 32px;
    border-radius: 5px 5px 5px 5px;
}

.prodTotal{
    text-align: right;

}


.prodTotal h1{

    text-align: right;
    font-weight: bold;
    position: relative;
    font-size: 20px;

}

