#header-slide{
    margin-top: 0px;
    height: 300px;
    text-align: center;
}
.slide-img{
    height: 150px;
    width: 300px;
    margin-top: 10px;
}

.slider-para{

    text-align: center;
    font-weight: bold;
}

#p1{
    padding:0px ; 
    border: 1px solid #dcdedc ; 
    border-radius: 10px 10px 0px 10px ; 
    background-color: var(--slide);
        
}

#p2{
    padding:0; 
    margin-top: 73px; 
    background-color: white
}

#p3{
    border: 1px solid #dcdedc ; 
    background-color: var(--slide) ;
    border-radius: 0px 0px 0px 10px ;
        
}

@media screen and (min-width: 0px) and (max-width: 940px) {
    #header-slide{
    display: none;
    }
  }