@import url('https://fonts.googleapis.com/css?family=Inconsolata:700');
 
 #recherche .container {
	 position: fixed;
	 margin: auto;
	 
	 left: 0;
	 right: 0;
	 bottom: 20px;
	 width: 300px;
	 height: 100px;
}
#recherche .container .search {
	 position: absolute;
	 margin: auto;
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 width: 50px;
	 height: 50px;
	 background: rgb(50, 0, 249);
	 border-radius: 50%;
	 transition: all 1s;
	 z-index: 400;
	 box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
}
#recherche .container .search:hover {
	 cursor: pointer;
}
#recherche .container .search::before {
	 content: "";
	 position: absolute;
	 margin: auto;
	 top: 22px;
	 right: 0;
	 bottom: 0;
	 left: 22px;
	 width: 12px;
	 height: 2px;
	 background: white;
	 transform: rotate(45deg);
	 transition: all 0.5s;
}
#recherche .container .search::after {
	 content: "";
	 position: absolute;
	 margin: auto;
	 top: -5px;
	 right: 0;
	 bottom: 0;
	 left: -5px;
	 width: 25px;
	 height: 25px;
	 border-radius: 50%;
	 border: 2px solid white;
	 transition: all 0.5s;
}
#recherche .container input {
	 font-family: 'Inconsolata', monospace;
	 position: absolute;
	 margin: auto;
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 width: 50px;
	 height: 50px;
	 outline: none;
	 border: none;
	 background: rgb(0, 17, 255);
	 color: white;
	 text-shadow: 0 0 10px rgb(20, 20, 220);
	 padding: 0 80px 0 20px;
	 border-radius: 30px;
	 box-shadow: 0 0 25px 0 rgb(101, 143, 240), 0 20px 25px 0 rgba(0, 0, 0, 0.2);
	 transition: all 1s;
	 opacity: 0;
	 z-index: 5;
	 font-weight: bolder;
	 letter-spacing: 0.1em;
}
#recherche .container input:hover {
	 cursor: pointer;
}
#recherche .container input:focus {
	 width: 300px;
	 opacity: 1;
	 cursor: text;
}
#recherche .container input:focus ~ .search {
	 right: -250px;
	 background: #151515;
	 z-index: 6;
}
#recherche .container input:focus ~ .search::before {
	 top: 0;
	 left: 0;
	 width: 25px;
}
#recherche .container input:focus ~ .search::after {
	 top: 0;
	 left: 0;
	 width: 25px;
	 height: 2px;
	 border: none;
	 background: white;
	 border-radius: 0%;
	 transform: rotate(-45deg);
}
#recherche .container input::placeholder {
	 color: white;
	 opacity: 0.5;
	 font-weight: bolder;
}
 