.carousel-item-padding-40-px{
    text-align: center;
    height: auto;
    margin: 0;
    padding: 0%;
    z-index: 1;
    
}

.slider button{
    z-index: 0;
    background-color: var(--slider);
    
}


.slider .image-grid{
    width: 100;
    height: 100px;
    margin-top: 0px;
    background-color: white;
    border: none;
}

.slider h1{
    font-size: 15px;
    font-weight: bold;
    margin-top: 5px;
}


.slider .bouton{
    margin-bottom: 5px;
    background-color: blue;
    color: white;
    border-radius: 5px 5px 5px 5px;
    border: 1px solid blue;
}

.slider .ct{
    border: 1px solid rgb(194, 194, 194);
    border-radius: 5px 5px 5px 5px;
    margin: 5px;
}

.slider .bouton:hover{
    background-color: green;
}

.slider .desc{
    width: calc(100% -30px);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin-left: 30px;

    margin-right: 30px;
}

.slider .ic{
    position: absolute;
    margin-left: 0;
    margin-top: -3px;
    width: 30px;
    text-align: center;
    margin-right: 5px;
    font-size: 20px;
    cursor: pointer;
}

.slider .ic:hover{
    color: orange;
}
