.contenu{
    height: auto;
    width: 100px;
    text-align: center;
    padding: 0!important;
}

 /* 
    05 94 90 61 74 

    05 06 32 57 50
*/


.contenu .image-grid{
    width: 100;
    height: 100px;
    margin-top: 4px;
    background-color: white;
}

.contenu h1{
    font-size: 15px;
    font-weight: bold;
    margin-top: 5px;
}

/* .contenu-block p{
    display: inline;
} */

.contenu button{
    margin-bottom: 5px;
    background-color: rgb(201, 201, 201);
    color: white;
    border-radius: 5px 5px 5px 5px;
    border: 1px solid  rgb(132, 131, 131);
    padding-left: 15px;
    padding-right: 15px;
}

.ct{
    border: 1px solid var(--text);
    border-radius: 15px 15px 15px 15px;
    margin: 5px;
    /* background-color: rgb(0, 21, 64); */
    color: var(--text);
    background-color: var(--backgroundColonne)
    
}

.ct:hover{
    border: 1px solid rgb(2, 2, 114);
    border-left: 2px solid orange;
    border-right: 2px solid  orange;

}

.contenu button:hover{
    background-color: green;
}

.contenu .desc{
    width: calc(100% -30px);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin-left: 30px;
  
    color: var(--text1);
    font-weight: bold;
    margin-right: 30px;
}

.ic{
    position: absolute;
    margin-left: 0;
    margin-top: -3px;
    width: 30px;
    text-align: center;
    margin-right: 5px;
    font-size: 20px;
    cursor: pointer;
}

.ic:hover{
    color: orange;
}